

.backSlider{
    background-color: #707070;
    padding: 50px;
    padding-top: 100px;
}

.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: transparent;
  overflow: visible !important;

  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}


.swiper-slide-active{  
    margin-top: 100px;
}

.swiper-slide:not(.swiper-slide-active , .swiper-slide-next){
    margin-top: 100px;
}


@media screen and (max-width:720px) {
  
  .backSlider{
   display: none;
}
  
}